h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.header {
  width: 100%;
  height: 104px;
  color: #fff;
  background-color: #008baa;
  font-family: Arial;
  font-style: normal;
  font-weight: 400;
}

.header_container {
  height: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header_logo-wrapper {
  height: 100%;
  align-items: center;
  display: flex;
}

.header_logo-wrapper > img {
  width: 179px;
  margin-top: -15px;
  margin-right: 22px;
}

.header_logo-wrapper > p {
  width: 150px;
  font-size: 16px;
  line-height: 18px;
}

.header_phone-wrapper {
  height: 100%;
  align-items: center;
  display: flex;
}

.header_phone-wrapper > a {
  color: inherit;
  margin-right: 8px;
  font-size: 30px;
  line-height: 34px;
  text-decoration: none;
}

.header_phone-wrapper > p {
  border-bottom: 1px dashed #ffffff40;
  font-size: 16px;
  line-height: 18px;
}

.header_social-wrapper {
  height: 100%;
  align-items: center;
  display: flex;
}

.header_social-wrapper > a {
  text-decoration: none;
}

.header_social-wrapper > a:not(:last-child) {
  margin-right: 16px;
}

.header_social-wrapper > a:first-child {
  display: none;
}

.header_social-wrapper > a:first-child img {
  border-radius: 50%;
}

.header_social-wrapper > a > img {
  width: 32px;
  height: 32px;
}

@media screen and (max-width: 850px) {
  .header_phone-wrapper > p {
    display: none;
  }
}

@media screen and (max-width: 700px) {
  .header_logo-wrapper > p {
    display: none;
  }
}

@media screen and (max-width: 550px) {
  .header_phone-wrapper {
    display: none;
  }

  .header_social-wrapper a:first-child {
    display: block;
  }
}

@media screen and (max-width: 400px) {
  .header_logo-wrapper > img {
    width: 130px;
  }
}

